.event-wrapper {
  font-family: Inter, sans-serif;
}

/* HERO */
.hero-card {
  position: relative;
}
.hero-card img {
  width: 100%;
  height: 420px;
  object-fit: cover;
  border-radius: 18px;
}
.badge-upcoming {
  position: absolute;
  top: 20px;
  left: 20px;
  background: #7a12ff;
  color: #fff;
  padding: 6px 14px;
  border-radius: 20px;
}
.slider-dots {
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
}
.slider-dots span {
  width: 8px;
  height: 8px;
  background: #ccc;
  display: inline-block;
  border-radius: 50%;
  margin: 0 4px;
}
.slider-dots .active {
  background: #7a12ff;
}

/* TITLE */
.title-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  gap: 20px;
}
.title-row h1 {
  font-weight: 700;
  text-transform: uppercase;
}
.meta-row {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 20px;
}
.meta-row div {
  display: flex;
  gap: 20px;
}
.description {
  padding-top: 25px;
}
.readmore {
  color: #7a12ff;
}

/* TAGS */
.tag {
  padding: 4px 12px;
  border-radius: 14px;
  font-size: 12px;
}
.yellow {
  background: #fff3cd;
}
.purple {
  background: #f2eaff;
  color: #7a12ff;
}
.green {
  background: #e6fffa;
}

/* BUTTONS */
.btn-register {
  background: #7a12ff;
  color: #fff;
  padding: 10px 26px;
  border-radius: 24px;
  border: none;
}
.btn-outline {
  border: 1px solid #7a12ff;
  color: #7a12ff;
  padding: 10px 26px;
  border-radius: 24px;
  background: #fff;
}

/* CARDS */
.card-box {
  background: #f6f6f6b5;
  padding: 18px;
  border-radius: 16px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.06);
}
.card-box h3 {
  font-size: 21px;
  font-weight: 800;
  padding-bottom: 20px;
}
.card-box h4 {
  color: #7713ec;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 700;
}
.discount-section {
  display: flex;
  align-items: center;
  gap: 15px;
}
.discount-section img {
  width: 50px;
}
.card-box p {
  padding-bottom: 20px;
}
.map-btn {
  border-radius: 50px;
  background: #f7f7f7;
  border: 1px solid rgb(219, 216, 216);
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.25);
  padding: 15px;
}
/* ================= Ticket Availability ================= */

.section-title {
  font-weight: 600;
}

/* CARD */
.ticket-card {
  background: #fafafa;
  border-radius: 16px;
  padding: 18px 20px;
  box-shadow: inset 0 8px 24px rgba(0, 0, 0, 0.06);
  height: 100%;
}
.ticket-title {
  max-width: 180px; /* adjust based on card width */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* TOP ROW */
.ticket-top {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  gap: 20px;
}

.ticket-icon {
  background: #fff3cd;
  color: #f5a623;
  padding: 6px 8px;
  border-radius: 8px;
  font-size: 12px;
}

.ticket-price {
  font-weight: 600;
}

/* TEXT */
.ticket-title {
  margin-bottom: 4px;
  font-weight: 600;
}

.ticket-date {
  font-size: 13px;
  color: #777;
  margin-bottom: 10px;
}

/* STATUS */
.ticket-status {
  font-size: 12px;
  padding: 4px 10px;
  border-radius: 12px;
  display: inline-block;
}

.ticket-status.live {
  background: #f2eaff;
  color: #7a12ff;
}

.ticket-status.upcoming {
  background: #fff3cd;
  color: #f5a623;
}

/* HOST */
.host-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
  border-radius: 15px;
  padding: 10px;
  box-shadow: inset 0 8px 24px rgba(0, 0, 0, 0.06);
}
.host-grid label {
  color: #1e1d1d;
  font-size: 15px;
  font-weight: 700;
}

/* TAG WRAP */
.tag-wrap span {
  padding: 4px 10px;
  border-radius: 12px;
  margin: 4px;
  display: inline-block;
}
.countdown {
  padding-top: 20px;
}

/* OTHER */
.countdown span {
  text-align: center;
  color: #0d0d0e;
  background: #f2eaff;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  margin-right: 23px;
  border: 3px solid #7713ec;
  padding: 25px;
  font-weight: 700;
  display: inline-block;
}

/* ACTION */
.action-row {
  display: flex;
  justify-content: space-around;
  gap: 20px;
  margin: 40px 0;
}
.event-back-btn {
  background: none;
  border: none;
  color: #5606a6;
  font-weight: 500;
  cursor: pointer;
  margin-bottom: 16px;
  padding: 0;
}

.event-back-btn:hover {
  text-decoration: underline;
}

/* RESPONSIVE */
@media (max-width: 768px) {
  .hero-card img {
    height: 260px;
  }
  .title-row {
    flex-direction: column;
  }
  .host-grid {
    grid-template-columns: 1fr;
  }
}
